



.header {
    background-color: $negro;
    padding: 1rem 0 3rem 0;

    .barra {
        a {
            font-weight: $bold;
            img {
                width: 15rem;
                height: 5rem;
            
            }
        }
    }

    &.inicio {
        background-position: center center;
        background-size: cover;

        @include tablet {
            min-height: 70rem;
        }

        .contenido-header {
            @include tablet {
                min-height: 70rem;
            }
        }
    }
}


.contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1 {
        text-align: left;
        color: $blanco;
        font-weight: $bold;
        line-height: 2;
        max-width: 60rem;
    }
}
