.dark-mode {
    background-color: darken($grisOscuro, 10);

    p {
        color: $blanco;
    }

    a, h1,h2,h3,h4  {
        color: $gris;
    }

    .anuncio, 
    .resumen-propiedad {
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;

        .precio {
            color: $verde;
        }
        .icono {
            filter: invert(100%);
        }
    }
    

    .texto-nosotros blockquote {
        color: $blanco;
    }
   
    .formulario {
        label,
        legend,
        p {
            color: $gris;
        }

        input:not([type="submit"]), 
        textarea,
        select {
            background-color: $grisOscuro;
            border: 1px solid $grisOscuro;
            color: $blanco;
        }
    }

    .s123_cuadro {
        border: $principal 2px solid;
        width: 8rem;
        height: 8rem;
        display: inline-block;
    }

  .perfil{
    background-color: darken($grisalternativo, 10);
    h1,h2{
       color: $principal;

    }
  }

  .boton-negro {
    @include boton($principal, inline-block);
    text-transform: uppercase;

    &:hover {
        color: $negro;
         
    }

    border-radius: 1rem;
}


}